/* display: flex; */

.sideHeader {
  height: 240px;
  display: flex;
  flex-direction: column;
}

.logo {
  height: 56px;
  width: 100%;
  /* background-color: red; */
}

.logo-svg {
  width: 76px;
  height: 32px;
  margin-top: 12px;
  margin-left: 32px;
  background-color: green;
}

.title {
  height: 104px;
  width: 100%;
  /* background-color: grey; */
  padding-left: 30px;
  padding-top: 42px;
}

.search {
  width: 100%;
  height: 32px;
  margin-top: 20px;
  /* background-color: blue; */
  display: flex;
}

.search-icon {
  width: 32px;
  height: 32px;
  background-color: green;
  margin-left: 30px;
}

textarea:focus,
input:focus {
  outline: none;
  /* color: var(--primary-light); */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--tetrary-light);
  opacity: 1; /* Firefox */
}

.input-prototypes {
  width: 100%;
  height: 32px;
  width: 376px;
  border: rgba(0, 0, 0, 0);
  border: 0;
  background-color: rgba(0, 0, 0, 0);
  margin-left: 10px;

  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: var(--primary-light);
  /* opacity: 0.2; */
}

.input-prototypes:focus {
  border: rgba(0, 0, 0, 0);
  border: 0;
}

.input-prototypes:active {
  border: rgba(0, 0, 0, 0);
  border: 0;
}
