:root {
  --background-primary-light: #000;
  --background-secondary-light: #222;
  --primary-light: rgba(255, 255, 255, 1);
  --secondary-light: rgba(255, 255, 255, 0.4);
  --tetrary-light: rgba(255, 255, 255, 0.2);
}

#root,
.App {
  height: 100%;
  background-color: var(--background-primary-light);
}

.test {
  height: 100%;
}

* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  text-rendering: optimizeLegibility;
  
  font-family: "Nunito", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.flex-container {
  display: flex;
  /* background-color: #f1eee9; */
  height: 100%;
  font-family: sans-serif;
}

.hiddenKeyHandler {
  overflow: hidden;
}

h1 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 44px;
  line-height: 32px;
  /* letter-spacing: 0.02em; */
  color: var(--primary-light);
}

h3 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.04em;
  color: #ffffff;
}

h4 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.04em;
}

