.prototypeItem {
  cursor: pointer;
  /* margin-top: 4px; */
  /* margin-bottom: 4px; */
}

.row-bg {
  display: flex;
  margin-left: 16px;
  padding-right: 16px;
  height: calc(100% - 4px);
  /* box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0.1); */
  background-color: rgba(0, 0, 0, 0);
}

.row-bg.active {
  border-radius: 16px;
  /* box-shadow: inset 0px -1px 0px rgba(255, 255, 255, 0); */
  background-color: var(--background-secondary-light);
}

.row-icon {
  width: 32px;
  height: 32px;
  margin-left: 14px;
  margin-top: 15px;
  margin-right: 12px;
  background-color: green;
}

.row-content {
  width: calc(100% - 48px);
  /* height: 45px; */
  margin-top: 10px;
  /* margin-bottom: 8px; */
  /* background-color: blue; */
}

.row-line-top {
  display: flex;
  /* flex-wrap: wrap; */
}

.row-title {
  flex: 100%;
  color: var(--secondary-light);
  margin-right: 10px;
}

.row-title.active {
  color: var(--primary-light);
}

.row-index {
  color: var(--secondary-light);
}

.row-line-bottom {
  display: flex;
  /* flex-wrap: wrap; */
}

.row-project {
  flex: 100%;
  color: var(--tetrary-light);
  /* max-width: calc(100%-50px); */
  /* background-color: violet; */
  margin-right: 10px;
}

.row-project.active {
  color: var(--secondary-light);
}

.row-year {
  color: var(--tetrary-light);
}

.row-year.active {
  color: var(--secondary-light);
}

